.wrap {
  height: 100vh;
  width: 100vw;
  /* background-color: #D6C6E1; */
  display: flex;
}

/* 사이드바 */
.sideBar {
  height: 100%;
  width: 18vw;
  padding: 10px;
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 10px;
  box-shadow: inset 0 2px 4px 0 rgb(0 0 0 / .05);
  background-color: #D6C6E1;
}

.sideBar.notion {
  border-right:2px solid white;
}

.sideBar.slack {
  border-left:2px solid white;
}

/* 버튼 */
.btn {
  width: 30px;
  height: 30px;
}

.btn.notion{

}

.btn.clear{
  display: block;
  position: absolute;
  right: 0;
  z-index: 1;
}

.btn.slack{

}

.btn.send {
  display: block;
  position: absolute;
  right: 0;
  right: 55px;
  top: 13px;
  z-index: 1;
}

/* 콘텐츠 */
.contentsWrap {
  width: 100%;
  height: 110px;
  position: relative;
  border: 2px solid white;
  border-radius: 10px;
  box-shadow: inset 0 2px 4px 0 rgb(0 0 0 / .05);
}

.contentsWrap.notion {

}

.content {
  width: 100%;
  height: 90px;
  top: 10px;
  position: absolute;
}

/* 채팅창 */
.chatWrap {
  width: 64vw;
  height: 100%;
  background-color: #F5F3F7;
  padding:10px;
  position: relative;
  box-shadow: inset 0 2px 4px 0 rgb(0 0 0 / .05);
  border: 2px solid white;
  border-radius: 10px;
  box-sizing: border-box;
}

.chatContainer {
  width: 99%;
  height: 110px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  border: 0.5px solid white;
  border-radius: 10px;
  padding: 5px;
  margin: 5px;
  color: #4A4A4A;
  box-shadow: inset 0 2px 4px 0 rgb(0 0 0 / .05);

}

.chatContainer.user {
  background-color: #D6C6E1;
}

.chatContainer.gpt {
  background-color: #9A73B5;
}

.pic {
  width: 70px;
  height: 70px;
  border: 1px solid white;
  border-radius: 10px;
}

.pic.user{
  text-align: center;
}

.pic.gpt{
  text-align: center;
}

.chat {
  flex-grow: 1;
  height: 70px;
  border: 1px solid white;
  border-radius: 10px;
}

.textAreaContainer {
  width: 100%;
  height: 150px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
}

.textAreaWrap {
  width: 70%;
  max-width: 70%;
  position: relative;
  background-color: #9A73B5;
}

.textArea {
  width: 41vw;
  border: 2px solid #D6C6E1;
  border-radius: 10px;
  resize: none;
  position: absolute;
  box-sizing: border-box;
  padding: 10px 50px 10px 10px;
}

